<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process18"></a>

<p><br></p>
<p><span class="rvts17">洛许（Lohse）:</span></p>
<p><img width="360" height="613" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber60.jpg"><span class="rvts18"></span></p>
<p><span class="rvts43"></span>
<br><span class="rvts21">洛许是所有源力者中最杰出的文艺青年。由于身体被某种神秘力量所控制，洛许的头脑中总会有些不太正常的想法。她的身体容易发生暴力事件。快去寻找控制自己的那个心魔，把它碎尸万段。</span>
<br><span class="rvts46"></span>
<br><span class="rvts29">种族：人类</span>
<br><span class="rvts21">默认职业：巫师（</span><span class="rvts20">Enchanter</span><span class="rvts21">）</span>
<br><span class="rvts46"></span>
<br><span class="rvts29">种族天赋：</span>
<br><span class="rvts21">洛许拥有人族的起始天赋。</span>
<br><span class="rvts46"></span>
<br><span class="rvts29">特有标签：</span>
<br><span class="rvts21">洛许只能被选为女性，并附带洛许特有标签。该标签可以使得她拥有一些特定的对话和行动。另外她还具备小丑和潜行者的标签。</span>
<br><span class="rvts46"></span>
<br><span class="rvts29">特殊技能：</span></p>
<div><table width="1206" border="1" cellpadding="0" cellspacing="-1" style="border-width: 0px; border-collapse: collapse;">
<tr valign="top">
<td width="103" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">图标</span></p>
</td>
<td width="190" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">英文</span></p>
</td>
<td width="104" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">中文</span></p>
</td>
<td width="754" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">说明</span></p>
</td>
</tr>
<tr valign="top">
<td width="103" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="64" height="64" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber61.jpg"></p>
</td>
<td width="190" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">Encourage</span></p>
</td>
<td width="104" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">鼓舞</span></p>
</td>
<td width="754" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">增加队友所有属性值。随着队友等级越高，增加的数值也会越高。</span></p>
</td>
</tr>
<tr valign="top">
<td width="103" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="64" height="64" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber62.jpg"></p>
</td>
<td width="190" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">Maddening Song</span></p>
</td>
<td width="104" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">疯狂之歌</span></p>
</td>
<td width="754" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">使目标区域敌人发疯，优先攻击它们附近的角色，</span>
<br><span class="rvts21">即便是它们的友军，持续</span><span class="rvts20">1</span><span class="rvts21">轮</span></p>
</td>
</tr>
</table>
</div>
<p class="rvps2"><span class="rvts48"></span>
<br><span class="rvts29">使用技巧：</span>
<br><span class="rvts20">1. </span><span class="rvts21">洛许拥有人族的好战天赋，配备着疯狂之歌的混乱天赋，更适合使用造成大量物理伤害的近战刺客型职业。</span>
<br><span class="rvts20">2. </span><span class="rvts21">充分利用洛许特殊技能</span><span class="rvts20"></span><span class="rvts21">疯狂之歌</span><span class="rvts20"></span><span class="rvts21">，制造团战优势。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



